.about {
  padding: 10px 0 40px 10px;

  @media (max-width: 734px) {
    padding: 0;
  }
  &__text {
    display: flex;
    flex-direction: column;
  }

  &__title {
    @include Font-Family-CookieR;
    align-self: center;
    font-size: 120px;
    line-height: 133px;
    color: $orange;

    @media (max-width: 518px) {
      font-size: 60px;
      line-height: 67px;
    }
  }

  // .about__subtitle

  &__subtitle {
    align-self: center;
    text-align: center;
    width: 480px;
    font-size: 16px;
    line-height: 24px;
    @include Font-Family-RubikR;
    margin-bottom: 60px;

    @media (max-width: 518px) {
      width: 288px;
    }
  }

  &__opasity {
    @media (max-width: 518px) {
      display: none;
    }
  }

  &__block_img {
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    gap: 27px;
  }

  &__photo {
    width: 200px;
  }

  &__digit {
    @include Font-Family-RubikL;
    text-align: center;
    font-size: 60px;
    line-height: 71px;
  }

  &__desc {
    @include Font-Family-RubikR;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $brown;
  }
}

.swiper-container {
  max-width: 1200px;
  height: 350px;
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.swiper-button-next {
  display: none;
  color: $orange;
  @media (max-width: 1108px) {
    display: block;
  }
}

.swiper-button-prev {
  display: none;
  color: $orange;

  @media (max-width: 1108px) {
    display: block;
  }
}

.swiper-pagination {
  top: 310px;
}

.swiper-pagination-bullet {
  background-color: $orange;
  width: 15px;
  height: 15px;
}
